import React from 'react';
import { Container, Card, Button, Tabs, Tab, Col, Row, Nav } from 'react-bootstrap';
import Experience from '../component/Experience';
import Jumbotron from '../component/Jumbotron';
import Certificates from '../page/Certificates'
import About from '../component/About';
import Experiences from './Experiences';
import Skills from '../component/Skills';
import { CVContext } from '../App';
import { RiLinkedinFill } from 'react-icons/ri';
import { VscGithub } from 'react-icons/vsc'
function Home() {
  const CV = React.useContext(CVContext);

  console.log(CVContext);

  const [key, setKey] = React.useState('home');
  return (
    <Container className="p-5">
      <Row>
        <Col lg={9} className="">
          <Card className='rounded bg-white'>
            <Card.Body className='p-5'>
              <Card.Title className='text-center display-6'>
                {CV.name}
              </Card.Title>
              <div className='d-flex justify-content-center'>
                <Button as={"a"} target="_blank" href={CV.social.linkedin}  className="m-1 btn-lg rounded">
                  <RiLinkedinFill />
                </Button>
                <Button as={"a"} target="_blank" href={CV.social.github} variant='dark' className="m-1 btn-lg rounded">
                  <VscGithub />
                </Button>
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col className='d-flex flex-column mb-5'>
          <Button size='lg' className='rounded mb-3'>Home</Button>
          <Button size='lg' className='rounded mb-3'>About</Button>
          <Button size='lg' className='rounded mb-3'>Experience</Button>
          <Button size='lg' className='rounded mb-3'>Skills</Button>
          <Button size='lg' className='rounded mb-3'>Work </Button>
          <Button size='lg' className='rounded mb-3'>Contact</Button>
        </Col>
      </Row>
    </Container>);
}

export default Home;
