import React from 'react';
import { Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';

function Certificate({ data }) {
  return <Card className='rounded bg-light h-100'>
    <Card.Body className='p-4'>
      <Card.Title>
        {
          data["name"]
        }
      </Card.Title>
      <Card.Text>
        {data["issuer"]}<br/>
        {data["year"]}&nbsp;
        <a href={data["verify"]} target="_blank">Verify</a>
      </Card.Text>
    </Card.Body>
  </Card>;
}

export default Certificate;
